<template>
    <div>
        <ul class="change">
            <!-- 姓名 -->
            <li><span>姓名</span><input type="text" style="padding-left:30px;" v-model="uname" placeholder="请输入姓名" name="uname"></li>
            <!-- 密码 -->
            <li><span>密码</span><input type="password" style="padding-left:30px;" v-model="upwd" placeholder="请输入密码" name="upw"></li>
            <!-- 性别 -->
            <li>
                <span></span>
                <div style="text-align:center;margin:0 auto;">
                    <input type="radio" style="height:13px;" value="1" v-model="sex" name="sex"  class="sex"><label for="1" style="margin:0px 80px 0 0px;text-align: left;color:#684029;font-size:16px;">男</label>
                    <input type="radio" value="0" v-model="sex" name="sex" class="sex"><label for="2" style="margin:0px 80px 0 0px;text-align: left;color:#684029;font-size:16px;">女</label>
                </div>
            </li>
            <!-- 生日 -->
            <li style="display:flex;">
                <span style="height:40px;">生日</span>
                <div class="row" style="height:40px;;margin:0;">
                    <date-picker style="margin:0;" :date="startTime" :option="option" :limit="limit"></date-picker>
                </div>
            </li>
            <!-- 邮箱 -->
            <li>
                <span style="height:40px;">邮箱</span>
                <input type="text" style="padding-left:30px;" placeholder="请输入邮箱" v-model="email">
            </li>
            <!-- 手机号 -->
            <li>
                <span style="height:40px;">手机号</span>
                <input type="text" style="padding-left:30px;" placeholder="请输入手机号" v-model="phone">
            </li>
        </ul>
        <div class="changeInfo">
            <button style="width:90%;height:45px;margin:30px 20px 70px 20px;background:#684029;color:#fff;" @click="changeinformation">保存</button>
        </div>
    </div>
</template>
<script>
import myDatepicker from 'vue-datepicker/vue-datepicker-es6.vue'
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            uid:"",
            upwd:"",
            sex:1,
            uname:"",
            email:"",
            phone:"",
            startTime: {
                time: '' },//点击后所获的日期值
            
             endtime: {
                time: '' },
             option: {
                type: 'day',
                week: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
                month: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
                format: 'YYYY-MM-DD',
                placeholder: '请选择生日?',
                inputStyle: {
                    'display': 'inline-block',
                    'width': '100%',
                    'padding': '6px',
                    'padding-left': '32px',
                    'line-height': '22px',
                    'font-size': '14px',
                    'border': '0',
                    'border-bottom':'2px solid #efefef',
                    'border-radius': '2px',
                    'color': '#5F5F5F'
                },
                color: {
                    header: '#1b98ee',
                    headerText: '#fff'
                },
                buttons: {
                    ok: '确定',
                    cancel: '取消'
                },
                overlayOpacity: 0.5,
                dismissible: true},
             timeoption: {
             type: 'min',
             week: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
             month: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
             format: 'YYYY-MM-DD HH:mm'
             },
             multiOption: {
             type: 'multi-day',
             week: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
             month: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
             format: 'YYYY-MM-DD HH:mm'
             },
             limit: [{
             type: Array,
              default: function _default () {
                return []
              }
             }]
        }
    },
    created(){
        this.uid=sessionStorage.getItem("uid");
        // this.changeinformation();
        this.information();
    },
    methods:{
        information(){
            
        },
        changeinformation(){
            var url="changeinfo";
             var postDate=this.qs.stringify({
                uid:this.uid,
                upwd:this.upwd,
                uname:this.uname,
                phone:this.phone,
                birth:this.startTime.time
            });
            console.log(this.startTime.time)
            this.axios.post(url,postDate).then(res=>{
                Toast(res.data.data)
                this.uid=""
                this.upwd=""
                this.uname=""
                this.phone=""
                this.startTime.time=""
                setTimeout(()=>{
                    this.$router.push("index")
                },500)
            })
        }
    },
    components: {
       'date-picker': myDatepicker
    }
}
</script>
<style>
.change{
   margin:10px 10px;
}
.change>li{
    width:100%;
    border-bottom:1px solid #f4f4f4; 
    display:flex;
    margin-top:10px;
    line-height: 40px;
    margin:0;
    padding:0;
    
}
.change>li>input{
    border:0;
    width:85%;
    margin:0;
    color: #442818;
    font-size:15px;
}
.change>li span{
    color: #442818;
    font-size:14px;
    display:inline-block;
    width:15%;
    text-align:center;
}
.sex{
   
}
</style>



