<template>
    <div class="app-type">
        <div class="header_top">
            <ul style="transition:all 0.5;list-style:none;padding:0;" :style="margin">
                <li :class="active==1?'border_bottom_active':'border_bottom_hide'" data-act="1" @click="pro_type">蛋糕</li>
                <li :class="active==2?'border_bottom_active':'border_bottom_hide'" data-act="2" @click="pro_type">冰淇淋</li>
                <li :class="active==3?'border_bottom_active':'border_bottom_hide'" data-act="3" @click="pro_type">咖啡下午茶</li>
                <li :class="active==4?'border_bottom_active':'border_bottom_hide'" data-act="4" @click="pro_type">面包</li>
                <li :class="active==5?'border_bottom_active':'border_bottom_hide'" data-act="5" @click="pro_type">常温蛋糕</li>
                <li :class="active==6?'border_bottom_active':'border_bottom_hide'" data-act="6" @click="pro_type">设计师礼品</li>
            </ul>
            
        </div>
        <div class="product_title" style="text-align:center">
            <p>蛋糕</p>
            <span>面包</span>
        </div>
        <div>
            <!-- 蛋糕 -->
            <div class="">
                <div style="width:50%;font-size:14px;">
                    <img src="img/type01.jpg" width="100%" alt="">
                    <span>Cocoa Island</span>
                    <p style="margin:0;font-size:14px;">可可岛</p>
                    <span >￥298.00/2.0磅</span>
                </div>
            </div>
        </div>
        
        
    </div>
</template>
<script>
export default {
    data(){
        return{
            active:1,
            margin:{
                marginLeft:"0px"
            }
        }
    },
    created(){

    },
    methods:{
        pro_type(e){
            this.active=e.target.dataset.act;
            if(this.active==1){
                this.margin.marginLeft="0px"
            }else if(this.active==2){
                this.margin.marginLeft="0px"
            }else if(this.active==3){
                this.margin.marginLeft="-20px"
            }else if(this.active==4){
                this.margin.marginLeft="-95px"
            }else if(this.active==5){
                this.margin.marginLeft="-120px"
            }else if(this.active==6){
                this.margin.marginLeft="-120px"
            }
        }
    }
}
</script>
<style>
.header_top{
    width: 100%;
    overflow: hidden;
    height: 40px;
}
.header_top>ul{
    height: 40px;
    width: 135%;
    transition: all .5s;
}
.header_top>ul>li{
    float: left;
    width: 82px;
    text-align: center;
    font-size: 14px;
    color: #442818;
    transition: all .5s;
}
.border_bottom_active{
    border-bottom: 2px solid #442818 ;
}
.border_bottom_hide{
    border-bottom: 2px solid transparent;
}
.product_title{
    height: 45px;
    border-bottom: 1px solid #efeeed;
    background: #fbf8f6;
    margin-bottom: 10px;
}
.product_title>p{
    margin: 0;
    padding-top: 3px;
    font-size: 15px;
    color: #442818;
    text-align: center;
}
.product_title>span{
    font-size: 14px;
    color: #D5BFA7;
    text-align: center;
}
</style>




