<template>
    <div class="person-Center">
        <div class="person_top">
            我的廿一客
        </div>
        <div class="icon_member" style="text-align:center;">
            <!-- 头像 -->
            <img src="img/ico_member.png" width="70px;" alt="">
            <ul style="list-style:none;margin:0;">
                <li>
                    <a href="javascript:;" style="color:#8e6a55;font-size:12px;">21cake</a>
                </li>
                <!-- 手机号 -->
                <li style="text-align:center;margin:0 auto;width:100px;padding-left:20px;color:#8e6a55;font-size:13px;background:url(img/phone.png) no-repeat 9px center;background-size:10px;">
                    {{phone}}
                </li>
                <li style="margin-top:5px;">
                    <!-- 修改个人信息按钮 -->
                    <button style="background:#684029;color:#fff;border-radius:7px;" @click="change">修改个人信息 ></button>
                </li>
            </ul>
        </div>
        <!-- 金额 优惠券 -->
        <div>
            <ul style="display:flex;list-style:none;">
                <li style="width:50%;text-align:center;color:#8e6a55;font-size:15px;border:1px solid #efefef;border-left:0;border-right:0;">
                    <p style="color:#442818;font-size:13px;margin:0;margin-top:10px;">￥0</p>
                    <p style="color:#8e6a55;font-size:13px;">果实币余额</p>
                </li>
                <li style="width:50%;text-align:center;color:#8e6a55;font-size:15px;border:1px solid #efefef;border-right:0;">
                    <p style="color:#442818;font-size:13px;margin:0;margin-top:10px;">0张</p>
                    <p style="color:#8e6a55;font-size:13px;">优惠券</p>
                </li>
            </ul>
        </div>
        <!-- 代付款 -->
        <ul style="list-style:none;">
            <li style="display:flex;justify-content:space-between;padding:0 10px 0 40px;background:#f6f6f6 url(img/member0.png) no-repeat 10px;background-size:22px;border-bottom:1px solid #D5D5D5;">
                <p style="margin:0;height:44px;line-height:44px;color:#8e6a55;">待付订单</p>
                <p style="height:44px;line-height:44px;margin:0;"><img src="img/go_next.png" width="10px;" alt=""></p>
            </li>
            <li style="display:flex;justify-content:space-between;padding:0 10px 0 40px;background:#f6f6f6 url(img/member2.png) no-repeat 9px;background-size:23px;border-bottom:1px solid #D5D5D5;">
                <p style="margin:0;height:44px;line-height:44px;color:#8e6a55;">全部订单</p>
                <p style="height:44px;line-height:44px;margin:0;"><img src="img/go_next.png" width="10px;" alt=""></p>
            </li>
            <li style="display:flex;justify-content:space-between;padding:0 10px 0 40px;background:#f6f6f6 url(img/member1.png) no-repeat 9px;background-size:23px;border-bottom:1px solid #D5D5D5;">
                <p style="margin:0;height:44px;line-height:44px;color:#8e6a55;">地理管理</p>
                <p style="height:44px;line-height:44px;margin:0;"><img src="img/go_next.png" width="10px;" alt=""></p>
            </li>
        </ul>
        <!-- 退出登录 -->
        <div class="goback" @click="remove">
            退出登录
        </div>
    </div>    
</template>

<script>
export default {
    data(){
        return{
            phone:''
        }
    },
    created(){
        this.search()
    },
    methods:{
        search(){
            var postDate=this.qs.stringify({
                uid:sessionStorage.getItem("uid")
            });
            var url="change";
            this.axios.post(url,postDate).then(res=>{
                this.phone=res.data.data[0].phone;
            })
        },
        change(){
            this.$router.push("/changeInfo")
        },
        remove(){
            this.$store.state.cartCount=0;
            sessionStorage.setItem("uid","");
            this.$router.push("/index");
        }
    }
}
</script>
<style>
.person_top{
    width: 100%;
    height: 44px;
    border-bottom: 1px solid #fcf1f1;
    color: #8e6a55;
    line-height: 44px;
    text-align: center;
    font-size: 16px;
    margin-bottom: 10px;
}
.goback{
    color: #442818;
    text-align: center;
    font-size: 14px;
    height: 50px;
    line-height: 50px;
    background: #f6f6f6;
    margin-top:40px;
}

</style>



